<template>
  <div>
    <div>
      <v-card class="pb-4" height="60">
        <v-spacer></v-spacer>
        <div class="mt-5 mx-5">
          <template v-if="$auth.check(['ADMIN', 'CASHIER'])">
            <v-form>
              <v-row wrap>
                <v-chip
                  class="subtitle-1 font-weight-bold my-3"
                  text-color="teal darken-1"
                  color="#f1f5fc"
                  medium
                  label
                >
                  {{
                    $vuetify.lang.t("$vuetify.cash_movement.cols.type.ENTRIES")
                  }}
                  : {{ get_sum_entry_movement }} FCFA
                  <!--{{ sum_zero| numberformat(locale, 0, "currency", "XAF")  }}--> </v-chip
                ><v-chip
                  class="subtitle-1 font-weight-bold my-3 ml-2"
                  text-color="red accent-4"
                  color="#f1f5fc"
                  medium
                  label
                >
                  {{
                    $vuetify.lang.t("$vuetify.cash_movement.cols.type.EXITS")
                  }}
                  : {{ get_sum_exit_movement }} FCFA
                  <!--{{ sum_zero| numberformat(locale, 0, "currency", "XAF")  }}-->
                </v-chip>
                <v-chip
                  class="subtitle-1 font-weight-bold my-3 ml-5"
                  text-color="black"
                  color="#f1f5fc"
                  medium
                  label
                >
                  {{
                    $vuetify.lang.t(
                      "$vuetify.item_movements.cols.period_of.title"
                    )
                  }}
                  :
                  <!--{{ sum_zero| numberformat(locale, 0, "currency", "XAF")  }}-->
                </v-chip>
                <v-menu
                  v-model="menu3"
                  color="#007bff"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="form.start_date"
                      color="#007bff"
                      class="font-weight-bold ml-2 my-2"
                      outlined
                      v-bind="attrs"
                      v-on="on"
                      dense
                      style="max-width: 100px"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="form.start_date"
                    color="#007bff"
                    @input="menu3 = false"
                  ></v-date-picker>
                </v-menu>
                <v-chip
                  class="subtitle-1 font-weight-bold my-3"
                  text-color="black"
                  color="#f1f5fc"
                  medium
                  label
                  style="margin-left: 10px"
                >
                  {{
                    $vuetify.lang.t(
                      "$vuetify.item_movements.cols.period_of.title2"
                    )
                  }}
                  :

                  <!--{{ sum_zero| numberformat(locale, 0, "currency", "XAF")  }}-->
                </v-chip>
                <v-menu
                  v-model="menu4"
                  color="#007bff"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="form.end_date"
                      color="#007bff"
                      class="font-weight-bold ml-2 my-2"
                      outlined
                      v-bind="attrs"
                      style="max-width: 100px"
                      v-on="on"
                      dense
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="form.end_date"
                    color="#007bff"
                    @input="menu4 = false"
                  ></v-date-picker>
                </v-menu>
                <v-btn
                  class="mr-2 my-2 ml-5"
                  @click="movement_analysis()"
                  color="teal darken-1"
                  light
                  :loading="loadingBtn"
                  style="margin-left: 15px"
                  :disabled="form.start_date === null || form.end_date === null"
                >
                  {{
                    $vuetify.lang.t(
                      "$vuetify.item_movements.cols.period_of.title3"
                    )
                  }}
                </v-btn>
              </v-row>
            </v-form>
          </template>
          <v-spacer></v-spacer>
        </div>
      </v-card>

      <v-card>
        <v-data-table
          :footer-props="{
            'items-per-page-options': itemPerPageOptions
          }"
          :headers="headersCash_movement"
          :items="itemsCash_movement"
          :items-per-page="itemPerPage"
          :loading="loading.list"
          :options.sync="pagination"
          :server-items-length="meta.totalElements"
          :sort-by="sortBy"
          :sort-desc="sortDesc"
          item-key="name"
          class="mt-5"
        >
          <template v-slot:header.code="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.create_date="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.cash="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.cashier="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.type="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.name="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.motive="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.name_movement="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.amount_movement="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>

          <template v-slot:body="{ items }">
            <v-hover>
              <tbody>
                <tr :key="item.id" v-for="item in items">
                  <td class="subtitle-2">{{ item.code }}</td>
                  <td class="subtitle-2">
                    {{ $moment(item.createdAt).format("YYYY-MM-DD HH:mm:ss") }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.cash.code }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.cash.user.username }}
                  </td>
                  <td class="subtitle-2">
                    <v-tooltip top>
                      <template
                        v-slot:activator="{ on }"
                        v-if="item.type === 'ENTRY'"
                      >
                        <v-icon
                          color="teal darken-1"
                          class="pointer"
                          dark
                          v-on="on"
                          style="font-size: 15px"
                        >
                          {{
                            $vuetify.lang.t(
                              "$vuetify.cash_movement.cols.type." + item.type
                            )
                          }}
                        </v-icon>
                      </template>
                      <template v-slot:activator="{ on }" v-else>
                        <v-icon
                          color="red accent-4"
                          class="pointer"
                          dark
                          v-on="on"
                          style="font-size: 15px"
                        >
                          {{
                            $vuetify.lang.t(
                              "$vuetify.cash_movement.cols.type." + item.type
                            )
                          }}
                        </v-icon>
                      </template>
                    </v-tooltip>
                  </td>
                  <td class="subtitle-2">
                    {{ item.name }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.motive }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.name_movement }}
                  </td>
                  <td class="subtitle-2">
                    <template v-if="item.type === 'ENTRY'">
                      <v-chip
                        class="ma-2"
                        color="teal darken-1"
                        text-color="white"
                        small
                      >
                        {{ item.amount_movement }} FCFA
                      </v-chip>
                    </template>
                    <template v-else>
                      <v-chip
                        class="ma-2"
                        color="red accent-4"
                        text-color="white"
                        small
                      >
                        {{ item.amount_movement }} FCFA
                      </v-chip>
                    </template>
                  </td>
                  <!--<td class="subtitle-2">-->
                  <!--<v-tooltip top>-->
                  <!--<template-->
                  <!--v-slot:activator="{ on }"-->
                  <!--v-if="item.type === 'ENTRY'"-->
                  <!--&gt;-->
                  <!--<v-icon-->
                  <!--color="teal darken-1"-->
                  <!--class="pointer"-->
                  <!--dark-->
                  <!--v-on="on"-->
                  <!--style="font-size: 15px"-->
                  <!--&gt;-->
                  <!--{{ item.amount_movement }} FCFA-->
                  <!--</v-icon>-->
                  <!--</template>-->
                  <!--<template v-slot:activator="{ on }" v-else>-->
                  <!--<v-icon-->
                  <!--color="red accent-4"-->
                  <!--class="pointer"-->
                  <!--dark-->
                  <!--v-on="on"-->
                  <!--style="font-size: 15px"-->
                  <!--&gt;-->
                  <!--{{ item.amount_movement }} FCFA-->
                  <!--</v-icon>-->
                  <!--</template>-->
                  <!--</v-tooltip>-->
                  <!--</td>-->
                </tr>
              </tbody>
            </v-hover>
          </template>
        </v-data-table>
        <filter-form
          ref="filterForm"
          :dialog="dialogForm"
          :filter="filter"
          @performFilter="performFilter"
        ></filter-form>
      </v-card>
    </div>
    <div>
      <div
        class="title mb-5 black--text blue lighten-5 text-right"
        style="height: 30px; margin-top: 30px"
      >
        {{ $vuetify.lang.t("$vuetify.patients_settlements.list.title") }}
      </div>
      <v-card>
        <v-data-table
          :footer-props="{
            'items-per-page-options': itemPerPageOptions
          }"
          :headers="headersSettlement"
          :items="itemsSettlement"
          :items-per-page="itemPerPage"
          :loading="loading.list"
          :options.sync="pagination"
          :server-items-length="meta.totalElements"
          :sort-by="sortBy"
          :sort-desc="sortDesc"
          item-key="name"
          class="mt-5"
        >
          <template v-slot:header.code="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.cash="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.date="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.code_patient="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.patient_name="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.payment="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:body="{ items }">
            <v-hover>
              <tbody >
                <tr :key="item.id" v-for="item in items">
                  <td class="subtitle-2">{{ item.code }}</td>
                  <td class="subtitle-2">
                    {{ $moment(item.date).format("YYYY-MM-DD") }}
                  </td>
                  <td class="subtitle-2">{{ item.cash.code }}</td>

                  <td class="subtitle-2">
                    {{ item.patient.code }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.patient.name }}
                  </td>
                  <td class="subtitle-2">
                    <v-chip
                      class="ma-2"
                      color="teal darken-1"
                      text-color="white"
                      small
                    >
                      {{ item.payment }} FCFA
                    </v-chip>
                  </td>
                </tr>
              </tbody>
            </v-hover>
          </template>
        </v-data-table>
        <filter-form
          ref="filterForm"
          :dialog="dialogForm"
          :filter="filter"
          @performFilter="performFilter"
        ></filter-form>
      </v-card>
    </div>
  </div>
</template>

<script>
import ListMixin from "./../../mixins/Common/List.vue";
import FilterForm from "./Filter";
export default {
  mixins: [ListMixin],
  data: () => ({
    menu4: false,
    menu3: false,
    menu2: false,
    search: null,
    menu: false,
    itemsSettlement: [],
    itemsCash_movement: [],
    headersSettlement: [],
    headersCash_movement: [],
    loadingBtn: false,
    urlItems: "cashs/movements_analysis",

    sortBy: ["id"],
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormCash: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    form: { start_date: null, end_date: null, product: null },
    filter: {
      code: null,
      role: null
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.cash_movement.delete.success"
    }
  }),
  computed: {
    get_sum_entry_settlement() {
      let sum = this._.sumBy(this.itemsSettlement, t => {
        return t.payment;
      });
      return sum;
    },
    get_sum_entry_movement() {
      let sum_entry = this._.filter(this.itemsCash_movement, t => {
        return t.type === "ENTRY";
      });
      let sum = this._.sumBy(sum_entry, t => {
        return t.amount_movement;
      });
      let total_sum = this.get_sum_entry_settlement + sum;
      return total_sum;
    },
    get_sum_exit_movement() {
      let sum_exit = this._.filter(this.itemsCash_movement, t => {
        return t.type === "EXIT";
      });
      // console.log(sum_entry);
      let sum = this._.sumBy(sum_exit, t => {
        return t.amount_movement;
      });
      return sum;
    }
  },
  created() {
    this.setHeadersCash_movement();
    this.setHeadersSettlement();
    //this.setItemRoles();
  },
  methods: {
    movement_analysis() {
      let self = this;
      this.getMovement_analysis();
      this.loading.refresh = true;
      this.getMovement_analysis()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loading.refresh = false;
        });
    },
    getMovement_analysis() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url:
              self.urlItems +
              "?start_date=" +
              this.form.start_date +
              "&end_date=" +
              this.form.end_date,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.itemsCash_movement = data.content.cash_movement;
            self.itemsSettlement = data.content.settlement;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },

    setHeadersCash_movement() {
      this.headersCash_movement = [
        {
          text: "$vuetify.cash_movement.cols.code.title",
          value: "code",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.created.title",
          value: "create_date",
          align: "start",
          sortable: false,
          width: "105",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.patients_settlements.cols.cash.title",
          value: "cash",
          align: "start",
          sortable: false,
          width: "105",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.patients_settlements.cols.cashier.title",
          value: "cashier",
          align: "start",
          sortable: false,
          width: "105",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.type.title",
          value: "type",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.name.title",
          value: "name",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.motive.title",
          value: "motive",
          align: "start",
          sortable: false,
          width: "20",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.name_movement.title",
          value: "name_movement",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.cash_movement.cols.amount_movement.title",
          value: "amount_movement",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },

        // {
        //   text: "$vuetify.cash_movement.cols.actions.title",
        //   value: null,
        //   align: "start",
        //   sortable: false,
        //   width: "140"
        // }
      ];
    },
    setHeadersSettlement() {
      this.headersSettlement = [
        {
          text: "$vuetify.patients_settlements.cols.code.title",
          value: "code",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.patients_settlements.cols.date.title",
          value: "date",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.patients_settlements.cols.cash.title",
          value: "cash",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.patients_settlements.cols.code_patient.title",
          value: "code_patient",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.patients_settlements.cols.patient_name.title",
          value: "patient_name",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.patients_settlements.cols.payment.title",
          value: "payment",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        // {
        //   text: "$vuetify.patients_settlements.cols.status.title",
        //   value: "is_active",
        //   align: "start",
        //   sortable: false,
        //   width: "1",
        //   class: "grey--text text--darken-3"
        // },
        // {
        //   text: "$vuetify.patients_settlements.cols.actions.title",
        //   value: null,
        //   align: "start",
        //   sortable: false,
        //   width: "140"
        // }
      ];
    },
    close() {
      // console.log(this.items[0].cash);
      this.dialogForm.shows.showInfo = true;
      this.messages = {
        title: "$vuetify.cash.cols.status.enabled",
        submit: "$vuetify.cash.update.submit",
        success: "$vuetify.cash.update.success"
      };
      this.$refs.closeCash.setForm(this.items[0].cash);
    },

    create() {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.cash_movement.new.title",
        submit: "$vuetify.cash_movement.new.submit",
        success: "$vuetify.cash_movement.new.success"
      };
      this.form = {
        id: null,
        type: null,
        name: null,
        motive: null,
        name_movement: null,
        code: null,
        is_active: false
      };
    },
    createCash() {
      this.dialogFormCash.show = true;
    },
    editUser(item) {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.cash_movement.update.title",
        submit: "$vuetify.cash_movement.update.submit",
        success: "$vuetify.cash_movement.update.success"
      };
      this.$refs.userForm.setForm(item);
    },
    showFilter() {
      this.dialogForm.shows.showFilter = true;
    },
    performFilter() {
      let self = this;
      self.loading.list = true;
      let params = {};
      if (self.filter.code !== null) {
        params["code"] = self.filter.code;
      }
      if (self.filter.role !== null) {
        params["role"] = self.filter.role;
      }
      self._search = params;
      //console.log("FILTRE", this._search);
      self
        .getItems()
        .then(() => {
          self.$refs.filterForm.closeDialog();
        })
        .finally(() => {
          self.$refs.filterForm.stopLoadingBtn();
        });
    },
    assignEnterprise(item) {
      this.dialogEnt.show = true;
      this.$refs.assignForm.setForm(item);
    }
  },
  components: {
    FilterForm
  }
};
</script>

<style scoped></style>
